body {
  background-image: url(../img/footer-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.logo__img {
  position: absolute;
  top: 0;
  left: 50%;
  width: 30%;
  transform: translateX(-50%);
  max-width: 300px;
  height: auto;
}